<script>
export default {
  data() {
    return {
      flag: "form",
      mobile: "",
      oldmobile: "18857458646",
      code: "",
      timeout: "发送验证消息",
      time: 60,
      canrun: true
    };
  },

  watch: {
    canrun(val, oldVal) {
      if (!val) {
        console.log(this.$refs.timeout.disabled);
      }
    }
  },

  components: {},

  computed: {
    username() {
      return this.$store.state.currentUser;
    }
  },

  mounted() {},

  methods: {
    startTimeout() {
      if (!this.canrun) {
        return;
      } else {
        this.canrun = false;
        var timeout_1 = setInterval(() => {
          this.time -= 1;
          if (this.time == 0) {
            this.time = 60;
            this.canrun = true;
            this.timeout = `发送验证消息`;
            window.clearInterval(timeout_1);
            return;
          }
          this.timeout = `发送验证消息(${this.time})`;
        }, 1000);
      }
    },
    submit() {}
  }
};
</script>
